@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

@import "forms";

/* ------ Add your CSS below this line (or @import your other CSS files). ------- */
#navCheckbox:checked ~ #navMobile{
	@apply block;
}


.pageHeader{
	@apply md:flex md:items-center md:justify-between;
}
.pageHeader .title{
	@apply min-w-0 flex-1;
}
.pageHeader .title h2{
	@apply text-2xl font-bold leading-7 text-gray-900 sm:truncate sm:text-3xl sm:tracking-tight;
}
.pageHeader .buttons{
	@apply mt-4 flex flex-col space-x-4 md:flex-row md:ml-4 md:mt-0;
}
.pageHeader .borderedButton{
	@apply inline-flex items-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50;
}
.pageHeader .button{
	@apply inline-flex items-center rounded-md bg-teal-500 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-teal-400 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-teal-600;
}


.mainTable{
	@apply min-w-full table-fixed divide-y divide-gray-300;
}
.mainTable thead tr th{
	@apply px-3 py-3.5 text-left text-sm font-semibold text-gray-900;
}
.mainTable tbody{
	@apply divide-y divide-gray-200 bg-white;
}
.mainTable tbody tr {
	@apply odd:bg-gray-50 hover:bg-teal-50;
}
.mainTable tbody tr td{
	@apply whitespace-nowrap px-3 py-4 text-sm text-gray-500;
}